.form {
  text-align: center;
  padding: 24px 20px 48px 20px;
}

.form__wrapper-fieldset {
  outline: none;
  border: none;

  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.form__fieldset {
  border: none;
  margin: 0;
  padding: 0;
  border-top: 1px solid #e5e5e5;
  padding-top: 24px;
  padding-bottom: 39px;

  @media (min-width: $tablet-width) {
    border: none;
  }
}

.form__fieldset-title {
  margin: 0;
  padding-bottom: 20px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  text-transform: uppercase;
}

.form__contact-item {
  display: flex;
  flex-direction: column;
}

.form__contact-item:not(:last-child) {
  margin-bottom: 16px;
}

.form__contact-label {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  text-transform: uppercase;
  margin-right: auto;
  margin-left: 16px;
}

.form__contact-input {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  padding: 16px;
  border: none;
  background-color: #f2f2f2;
}

.form__options {
  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: repeat(3, 1fr);
  }
}

.form__option--attraction {
  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
  }
}

.form__options-item:not(:last-child) {
  margin-bottom: 36px;
}

.form__options-label {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  position: relative;
}

.form__options-input:checked + .check-mark::before {
  background-image: url("../img/tick.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 12px 10px;
}

.control-mark {
  content: "";
  position: absolute;
  top: -10px;
  left: -62px;
  width: 46px;
  height: 46px;
  background: #f2f2f2;
  border-radius: 50%;
}

.form__submit {
  width: 281px;
  margin-bottom: 3px;
  cursor: pointer;

  @media (min-width: $tablet-width) {
    width: 324px;
    margin-left: 0;
  }
}

.form__note {
  font-weight: 400;
  font-size: 12px;
  line-height: 21px;
  text-align: end;
  color: rgba(51, 51, 51, 0.6);
  text-transform: uppercase;
  display: block;
}

.form__input-wrapper {
  padding-right: 57px;
}

.form__input-wrapper--phone {
  background-image: url("../img/icon-phone.svg");
  background-repeat: no-repeat;
  background-position: right;
}

.form__input-wrapper--mail {
  background-image: url("../img/icon-mail.svg");
  background-repeat: no-repeat;
  background-position: right;
}

.form__options-input {
  padding-left: 62px;
}

.form__options-input:checked + .control-mark::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 16px;
  height: 16px;
  background-color: #333333;
  border-radius: 50%;
  transform: translate(-50%, -50%);
}

.check-mark {
  content: "";
  position: absolute;
  top: -10px;
  left: -62px;
  width: 46px;
  height: 46px;
  background: #f2f2f2;
  border-radius: 4px;
}

.form__fieldset--recall textarea {
  border: none;
  background-color: #f2f2f2;
  min-height: 150px;
  min-width: 279px;
  padding-top: 16px;
  padding-left: 16px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;

  @media (min-width: $tablet-width) {
    min-width: 648px;
    min-height: 160px;
  }

  @media (min-width: $desktop-width) {
    min-width: 768px;
  }
}

.form__fieldset--submit {
  width: 281px;
  margin: auto;

  @media (min-width: $tablet-width) {
    display: flex;
    width: 648px;
    justify-content: space-between;
  }

  @media (min-width: $desktop-width) {
    width: 768px;
  }
}
