.sights {
  @media (min-width: $tablet-width) {
padding: 0 60px;
  }
}

.sights__list {
  @media (min-width: $tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  @media (min-width: $desktop-width) {
    grid-template-columns: repeat(3, 1fr);
  }
}

.sights__item {
  padding: 25px 20px;
  border-bottom: 1px solid #e5e5e5;

  @media (min-width: $tablet-width) {
    border-bottom: none;
    padding: 0;
  }
}

.sights__item:first-child {
  grid-column: 1/-1;
  border-top: 1px solid #e5e5e5;

  @media (min-width: $tablet-width) {
    border-top: none;
  }
}

.sights__image {
  padding-bottom: 15px;
}

.sights__wrapper {
  @media (min-width: $tablet-width) {
    display: flex;
    justify-content: space-between;
    padding: 22px 20px 15px 20px;
    background-color: #f2f2f2;
  }

  @media (min-width: $desktop-width) {
    background-color: transparent;
  }
}

.sights__image--road {
  @media (min-width: $tablet-width) {
    background-image: url("../img/tablet-road.jpg");
    background-repeat: no-repeat;
    background-size: 100%, cover;
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/desktop-road.jpg");
    background-repeat: no-repeat;
    background-size: 100%, cover;
  }
}

.sights__image--plant {
  @media (min-width: $tablet-width) {
    background-image: url("../img/tablet-plant.jpg");
    background-repeat: no-repeat;
    background-size: 100%, cover;
    min-height: 267px;
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/desktop-plant.jpg");
    background-repeat: no-repeat;
    background-size: 100%, cover;
  }
}

.sights__image--forest-road {
  @media (min-width: $tablet-width) {
    background-image: url("../img/tablet-forest-road.jpg");
    background-repeat: no-repeat;
    background-size: 100%, cover;
    min-height: 267px;
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/desktop-forest-road.jpg");
    background-repeat: no-repeat;
    background-size: 100%, cover;
  }
}

.sights__image--bridge {
  @media (min-width: $tablet-width) {
    background-image: url("../img/tablet-bridge.jpg");
    background-repeat: no-repeat;
    background-size: 100%, cover;
    min-height: 267px;
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/desktop-bridge.jpg");
    background-repeat: no-repeat;
    background-size: 100%, cover;
  }
}

.sights__description {
  margin-bottom: 11px;
}

.sights__item:first-child .sights__image {
  height: 400px;
}

.sights__image--first img {
  @media (min-width: $tablet-width) {
    width: 648px;
  }
}

.sights__image img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.sights__title {
  margin: 0;
  font-size: 14px;
  line-height: 21px;
  color: #000000;
}

.sights__author {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
}

.sights__like {
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  color: #000000;
  padding-left: 37px;
  background-color: transparent;
  border: none;
  background-image: url("../img/like-icon.svg");
  background-repeat: no-repeat;
  background-position: left;
}
